<docs>
# TheHorizontalNavigation
> The horizontal nav in the theme page

@author Kffein <contact@kffein.com>
</docs>


<script lang="ts">
import { templateRef } from '@vueuse/core';
import { findIndex } from "lodash";
import { store as themeStore } from 'pages/Thematique/store';
import { store as i18nStore } from 'plugins/i18n/store';
import emitter, { EVENTS } from 'plugins/store/events';
import { store as globalsStore } from 'plugins/store/globals';
import { store as appStore } from 'store/app';
import { store as interfaceStore } from 'store/interface';
import { gsap } from "vendors/gsap";
import { computed, defineComponent, reactive, ref, toRefs, watch } from 'vue';
import { useRoute, useRouter } from 'vue-router';

const HorizontalNavigation = defineComponent({
  name: 'TheHorizontalNavigation',
  setup() {

    const router = useRouter();
    const route = useRoute();
    const isDragging = ref(false);

    const el = templateRef<HTMLElement>("el");
    const i18nLocale: Koddein.Locale = i18nStore.locale as Koddein.Locale;

    const disabled = computed(() => appStore.disabled);
    const hasTouch = computed(() => interfaceStore.hasTouch);
    const variant = computed(() => themeStore.current?.id);
    const showingNav = computed(() => themeStore.showThemeNav);
    const isForceThemeNav = computed(() => themeStore.forceThemeNav);
    const isIntro = computed(() => themeStore.isIntro);
    const isAPageTransition = computed(() => appStore.isAPageTransition);
    const isThemeRoute = computed(() => route.name === 'thematique')
    const ThemeSwiper = ref<any>();

    // Check if we need to display or not the 
    watch(
      showingNav,
      (bool) => {
        if (bool) {
          if (el.value.classList.contains("hidden") && !themeStore.fromListing) animateIn();
        }
        else {
          if (!isThemeRoute.value || isIntro.value || isAPageTransition.value || isForceThemeNav.value) animateOut();
        }
      }
    )

    // Watch route to slide to a specific direction
    watch(
      route,
      () => {
        const index = findIndex(globalsStore.themes, (theme: any) => theme.url === `/${route.params.slug}/`);
        if (index !== -1) {
          setTimeout(() => {
            ThemeSwiper.value?.swiper?.slideTo(index, 1200);
          }, 500);
        }
      },
      {
        deep: true,
        immediate: true
      }
    )

    const data = reactive({
      optionsSlider: {
        loop: false,
        cursor: false,
        freeMode: {
          enabled: true,
          sticky: false,
        },
        touchStartForcePreventDefault: true,
        mousewheel: !hasTouch.value ? {
          invert: false,
        } : undefined,
        spaceBetween: 10,
        pagination: false,
        centeredSlides: false,
        slidesPerView: 'auto',
        on: {
          touchStart: () => {
            isDragging.value = false;
          },
          touchMove: () => {
            isDragging.value = true;
          },
          touchEnd: () => {
            setTimeout(() => {
              isDragging.value = false;
            }, 0);
          },
          sliderFirstMove: () => {
            isDragging.value = true;
          },
          transitionEnd: () => {
            isDragging.value = false;
          },
        },
      },
    })

    // Tween to display elements 
    const animateIn = () => {
      gsap.timeline()
        .add(() => {
          el.value.classList.remove("hidden");
        }, 0)
        .from(el.value.querySelectorAll(".link-wrapper"), { yPercent: 150, ease: "power4.out", duration: .75, stagger: 0.05 }, 0.1)
    }

    // Tween to hide elements 
    const animateOut = () => {
      const speed = !isForceThemeNav.value ? 1 : 0.7;

      gsap.timeline()
        .to(el.value, { yPercent: 130, ease: "power4.inOut", clearProps: "all", duration: speed }, 0)
        .add(() => {
          el.value.classList.add("hidden");
        }, speed - 0.2)
    }

    // Switch thematique
    const onClick = (e: PointerEvent, theme: ThemeItemCTA) => {
      e.preventDefault();
      if (isDragging.value) {
        return
      }

      //same route - do nothing
      if (route.path === theme.url) return;

      gsap.timeline()
        .add(() => {
          emitter.emit(EVENTS.THEME_SWITCHING);
        }, 0)
        .add(() => {
          themeStore.setNextVariant(theme.id);
          router.push({ path: theme.url });
        }, .8)
    }

    return {
      ...toRefs(data),
      disabled,
      currentVariant: computed(() => themeStore.nextVariant || variant.value),
      locale: i18nLocale?.name,
      themes: computed(() => globalsStore?.themes),
      onClick,
      ThemeSwiper,
    }
  },
})
export default HorizontalNavigation
</script>

<template>
  <div ref="el"
       class="TheHorizontalNavigation hidden"
       :data-disabled="disabled"
       :data-debug="currentVariant"
       data-lenis-prevent
  >
    <nav v-if="themes" class="nav">
      <ui-swiper ref="ThemeSwiper"
                 class="links"
                 :options="optionsSlider"
                 :cursor="false"
                 :overflow="true"
      >
        <li v-for="(theme, i) in themes"
            :key="`nav-main-${i}`"
            class="link-wrapper"
        >
          <ui-button class="link"
                     theme="theme"
                     :selected="theme.id === currentVariant"
                     :label="theme.title"
                     :subtitle="theme.subtitle"
                     :variant="currentVariant"
                     :href="theme.url"
                     tag="a"
                     @click="onClick($event, theme)"
          />
        </li>
      </ui-swiper>
    </nav>
  </div>
</template>

<style lang="stylus" scoped>
.TheHorizontalNavigation
  position fixed
  left 0
  bottom 10px
  width 100vw
  z-index $z-nav

  &[data-disabled="true"]
    pointer-events none

  &.hidden
    pointer-events none
    opacity 0
  
  .nav 
    width 100%
    padding-left $gutter
    padding-right $gutter
  
  .link
    text-align center
    width auto

  .link-wrapper
    rp(width, 300px)

</style>
