<docs>
# TheNavigation
> Exists only if nav is active in store/nav.ts

@author Sebastien Levy <sebastien@kffein.com>
</docs>


<script lang="ts">
import { templateRef } from '@vueuse/core';
import { store as scrollStore } from 'components/SmoothScroll/store';
import { store as themeStore } from 'pages/Thematique/store';
import { store as i18nStore } from 'plugins/i18n/store';
import emitter, { EVENTS } from 'plugins/store/events';
import { store as globalsStore } from 'plugins/store/globals';
import { interactWithSceneAt } from 'plugins/webgl/three';
import { gsap } from 'vendors/gsap';
import { computed, defineComponent, watch } from 'vue';
import { useRoute } from 'vue-router';
import { store as NavStore } from './store';

const Navigation = defineComponent({
  name: 'TheNavigation',
  setup() {
    const el = templateRef<HTMLElement>('el')
    const isOpen = computed(() => NavStore.isOpen);
    const i18nLocale: Koddein.Locale = i18nStore.locale as Koddein.Locale;
    const route = useRoute();
    
    /**
     * Close nav when keydown on Escape
     */
    const escapeBinding = (e:any):void => {
      if(e.key === 'Escape') closeNav();
    } 

    const closeNav = () => {
      NavStore.openState(false);
    }
    
    const handleClick = (e:PointerEvent, url:string) => {
      NavStore.openState(!isOpen.value);
      e.preventDefault();

      if(route.name === "home") {
        interactWithSceneAt(1, {
          callback: "exitFromMainHeader",
          params: url
        })
      }
      else{
        emitter.emit(EVENTS.PAGE_TRANSITION, {url, bgColor: themeStore.getBgColorFromURL(url)});
      }
    }

    /**
     * Observe navState and play animation
     */
    watch(
      isOpen,
      (open:any) => {
        if(open) {
          el.value.scrollTop = 0;

          const tlOpen = gsap.timeline({ paused: true })
            .fromTo(el.value?.querySelectorAll(".link-wrapper"), { opacity:1, xPercent:100}, { opacity:1, xPercent: 0, duration: 0.75, ease: 'power4.out', overwrite: false, stagger: 0.05 }, 0)

          window.addEventListener('keydown', escapeBinding);
          scrollStore.setScrollable(false);
          tlOpen.play();
        }
        else { 
          const tlClose = gsap.timeline({ paused: true })
            .to(el.value?.querySelectorAll(".link-wrapper"), { xPercent: '100', duration: 0.5, ease: 'power4.out', overwrite: true, stagger:0.05 })

          window.removeEventListener('keydown', escapeBinding);
          scrollStore.setScrollable(true);
          tlClose.play();
        }

      },
      { deep: false },
    );

    return {
      isOpen,
      locale: i18nLocale?.name,
      onClick: handleClick,
      closeNav,
      themes: computed( () => globalsStore?.themes),
    }
  },
})
export default Navigation
</script>

<template>
  <div ref="el"
       class="TheNavigation"
       :data-is-open="isOpen"
       data-lenis-prevent
  >
    <div class="wrapper">
      <nav v-if="themes" class="nav">
        <ul class="links">
          <li v-for="(theme, i) in themes"
              :key="`nav-main-${i}`"
              class="link-wrapper"
          >
            <ui-button class="link"
                       theme="theme"
                       :label="theme.title"
                       :subtitle="theme.subtitle"
                       :href="theme.url"
                       tag="a"
                       @click="onClick($event, theme.url)"
            />
          </li>
        </ul>
      </nav>
    </div>
    <div class="colorfill" @click="closeNav" />
  </div>
</template>

